"use client";

import { useEffect, useState, useRef } from "react";
//import { Container, Grid, Card, CardContent, CardActions, CardHeader, Box, Button, Typography } from "@mui/material";
import { Container, Grid, Card, CardContent, CardActions, CardHeader, Box, Button, Typography } from "@mui/material";
import { Link } from "next/link";

export default function HomePage({ type = "Movie" }) {
    const [top250, setTop250] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const genres = ["Comedy", "Romance", "Fantasy", "Music", "Animation", "Documentary", "Horror", "Sport", "Drama"];
    const years = [2023, 2022, 2021, 2020, 2019, 2018, 2017, 2016, 2015];
    const typeMap = {
        Movie: "Movies",
        Short: "Shorts",
        TVSeries: "TV Series",
    };
    const imgGenre = type === "Movie" ? "" : type === "Short" ? "s" : "t";
    const typeRef = useRef(type);

    useEffect(() => {
        // Simulate fetching data
        setLoading(true);
        setTimeout(() => {
            setTop250([
                { titleId: "1", primaryTitle: "Inception", averageRating: 8.8 },
                { titleId: "2", primaryTitle: "The Dark Knight", averageRating: 9.0 },
            ]);
            setLoading(false);
        }, 2000);
    }, [type]);

    return (
        <Container component="main" sx={{ py: 4 }}>
            {/* Top 250 Section */}
            <Typography variant="h4" gutterBottom>
                Top 250 {typeMap[type]}
            </Typography>
            <Grid container spacing={4}>
                <Grid item xs={12} sm={6} md={4}>
                    <Card
                        sx={{
                            backgroundImage: `linear-gradient(rgba(0,0,0,0.6), rgba(0,0,0,0.6)), url(/${imgGenre}1.png)`,
                            backgroundSize: "cover",
                            color: "white",
                            height: "100%",
                            display: "flex",
                            flexDirection: "column",
                        }}
                    >
                        <CardHeader
                            title={`Top 250 ${typeMap[type]}`}
                            titleTypographyProps={{ variant: "h5", align: "center" }}
                        />
                        <CardContent sx={{ flexGrow: 1 }}>
                            {loading && (
                                <Typography align="center" variant="subtitle1">
                                    Loading...
                                </Typography>
                            )}
                            {error && (
                                <Typography align="center" variant="subtitle1">
                                    Error: {error.message}
                                </Typography>
                            )}
                            {!loading && !error && (
                                <ol>
                                    {top250.slice(0, 5).map((movie) => (
                                        <Typography component="li" key={movie.titleId} variant="subtitle1">
                                            <Link href={`/production_info/${movie.titleId}`} style={{ color: "white", textDecoration: "none" }}>
                                                {movie.primaryTitle}: {movie.averageRating}
                                            </Link>
                                        </Typography>
                                    ))}
                                </ol>
                            )}
                        </CardContent>
                        <CardActions>
                            <Button fullWidth>
                                <Link href="/TopInfoPage" style={{ color: "white", textDecoration: "none" }}>
                                    Show More
                                </Link>
                            </Button>
                        </CardActions>
                    </Card>
                </Grid>
            </Grid>

            {/* Top 20 by Genre */}
            <Typography variant="h5" gutterBottom sx={{ mt: 4 }}>
                Top 20 for Each Genre
            </Typography>
            <Grid container spacing={4}>
                {genres.map((genre, index) => (
                    <Grid item xs={12} sm={6} md={4} key={index}>
                        <Card>
                            <CardHeader
                                title={genre}
                                titleTypographyProps={{ variant: "h6", align: "center" }}
                            />
                            <CardContent>
                                <Typography align="center">Top movies in {genre}</Typography>
                            </CardContent>
                        </Card>
                    </Grid>
                ))}
            </Grid>

            {/* Top 20 by Year */}
            <Typography variant="h5" gutterBottom sx={{ mt: 4 }}>
                Top 20 for Each Year
            </Typography>
            <Grid container spacing={4}>
                {years.map((year, index) => (
                    <Grid item xs={12} sm={6} md={4} key={index}>
                        <Card>
                            <CardHeader
                                title={year}
                                titleTypographyProps={{ variant: "h6", align: "center" }}
                            />
                            <CardContent>
                                <Typography align="center">Top movies from {year}</Typography>
                            </CardContent>
                        </Card>
                    </Grid>
                ))}
            </Grid>
        </Container>
    );
}
